	module.exports = {"data":{"wordpressPost":{"title":"How To Stay Young Forever (If Halle Berry Can Do It So Can You)","content":"<p><a href=\"https://www.instagram.com/p/BUxrRAvgZaJ/?taken-by=halleberry&amp;hl=en\">Halle Berry</a> hit the red carpet the other week and we could not stop looking at her. Those legs, that cleavage, her mesmerising smile, but most of all that incredible dewy, fresh, glowing skin.</p>\n<p>The woman is 50 years old and yet she could easily pass for half that. Sure, she&#8217;s been blessed with awesome genes, but she&#8217;s also taken great care of her skin &#8211; and so she looks like she does.<br />\nIf you fancy a little bit of that for yourself, (and damn, who doesn&#8217;t?), here&#8217;s a few simple steps you&#8217;ll want to start including in your skincare regime.</p>\n<p><strong>Say No To The Sun</strong><br />\nThe first and most important step &#8211; protect yourself from the sun’s harsh rays.<br />\nThis means applying roughly 1/4 of a teaspoon of a high-level sunscreen (we’re talking no less than an SPF of 30+) to your face. You’ll also need to apply that same amount again to your neck and again to your chest if it’s going to be exposed to the sun.<br />\nDon’t think makeup or BB creams with SPF built-in are enough. They’re not.<br />\n“You won’t use enough and you won’t apply it evenly enough,” says <a href=\"http://www.gotoskincare.com\">Zoe Foster Blake</a>, founder of Go To Skincare and author of Amazinger Face (Penguin).<br />\n“By all means use foundation with SPF, but layer it on top of broad-spectrum sun protection,” she says.</p>\n<figure id=\"attachment_2344\" style=\"width: 239px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2344\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16220938/JPG-Halle-arm-239x300.jpg\" alt=\"\" width=\"239\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16220938/JPG-Halle-arm-239x300.jpg 239w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16220938/JPG-Halle-arm.jpg 475w\" sizes=\"(max-width: 239px) 100vw, 239px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@halleberry</figcaption></figure>\n<p><strong>Care For Your Skin</strong><br />\nEach morning cleanse skin thoroughly. Skip the foaming cleansers and those that double as scrubs. Both types are likely to be too harsh for mature skin. Instead, opt for cream versions or cleansing oils, which are more gentle.<br />\nOnce skin is cleansed each morning apply a treatment serum. Use one that is specifically designed to treat any issues you have such as redness, pigmentation or signs of ageing. Allow at least five, preferably 10 minutes for your serum to fully penetrate the skin and be absorbed (otherwise you risk wiping it off during this next step). Then, apply a quality moisturiser with an SPF of 30+ or more. Opt for one that blocks both UVA and UVB rays. Remember to do your neck and chest too if needed. You might like to apply cream to your hands also.<br />\nEach evening repeat the process. However, swap your moisturiser for one that’s designed specifically for night-time use. These contain no SPF and are generally more rich.</p>\n<figure id=\"attachment_2345\" style=\"width: 237px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2345\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16221024/JPG-Halle-mirror-237x300.jpg\" alt=\"\" width=\"237\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16221024/JPG-Halle-mirror-237x300.jpg 237w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16221024/JPG-Halle-mirror.jpg 473w\" sizes=\"(max-width: 237px) 100vw, 237px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@halleberry</figcaption></figure>\n<p><strong>Adopt A Positive Attitude</strong><br />\nDespite a universal shift in how we view women and ageing, many of us continue to struggle with self image. If that sounds likes you, try looking in the mirror each morning and finding something to compliment. Yes it will may feel a little strange (at least at first) but this practise is thought to boost feelings of wellbeing and self esteem.<br />\nIt may also pay to remind yourself that the concept of the ‘ideal’ woman that we see in movies, on TV and in many magazines is often unrealistic and unachievable for just about everyone. And, there’s this &#8230; recent research published by the British Psychological Society shows that the majority of women who use skincare and makeup do so not to look younger, but to look ‘good’ and ‘to feel confident’. Why not be one of them.</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/06/16220854/JPG-Halle-hair.jpg"},"categories":[{"slug":"lifestyle","name":"Lifestyle"},{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"c0f48be9-3e82-5a2b-be1c-fc9569d0cca0"}}

